/*eslint-disable*/
import { useForm } from "react-hook-form"
import { useEffect } from "react"
import { formStateProps } from "../type/props"

const FormState = ({setError,clearErrors,setWatch}:formStateProps) => {
    const {register,handleSubmit,watch} = useForm<search>()
    
    const valed = (data:search ) => {
        if(data.movie_title?.length as number <= 2) 
        setError("movie_title",{message:"You must enter at least 2 characters."})
        else setError("movie_title_result",{message:`Search results for "${data.movie_title}".`})}
    
    useEffect(()=>{
        setWatch({movie_title:watch().movie_title as string})
    },[watch().movie_title,setWatch,watch])

    return(<form className="text-center flex justify-center" onSubmit={handleSubmit(valed)}> 
        <input type = "text" placeholder="movie_title" 
        {...register("movie_title",{required:true, minLength:1})}
        className="border-slate-200 border-2 w-96 rounded-xl p-2 mr-3 shadow-xl"/>
       <div className=" mt-1 flex">
       <svg className="w-8 h-8 rounded-xl bg-slate-100 shadow-lg hover:bg-slate-200 hover:text-pink-200" 
       fill="currentColor" onClick={handleSubmit(valed)}
          viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"></path>
          </svg >

          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" 
          strokeWidth={1.5} stroke="currentColor" onClick={()=>clearErrors()}
          className="w-8 h-8 ml-2 bg-slate-100 shadow-lg hover:bg-slate-200 rounded-xl hover:text-pink-200">
  <path strokeLinecap="round" strokeLinejoin="round" 
  d="M11.42 15.17L17.25 21A2.652 2.652 0 0021 17.25l-5.877-5.877M11.42 15.17l2.496-3.03c.317-.384.74-.626 1.208-.766M11.42 15.17l-4.655 5.653a2.548 2.548 0 11-3.586-3.586l6.837-5.63m5.108-.233c.55-.164 1.163-.188 1.743-.14a4.5 4.5 0 004.486-6.336l-3.276 3.277a3.004 3.004 0 01-2.25-2.25l3.276-3.276a4.5 4.5 0 00-6.336 4.486c.091 1.076-.071 2.264-.904 2.95l-.102.085m-1.745 1.437L5.909 7.5H4.5L2.25 3.75l1.5-1.5L7.5 4.5v1.409l4.26 4.26m-1.745 1.437l1.745-1.437m6.615 8.206L15.75 15.75M4.867 19.125h.008v.008h-.008v-.008z" />
</svg>
       </div>
    </form>)}
export default FormState