import { useNavigate } from "react-router-dom"
import { useRecoilState } from "recoil"
import { cartData } from "../../atom"

const DetailSvg = (detail:movieType) => {
    const navigate = useNavigate()
    const [cart,setCart] = useRecoilState(cartData)
    return( <footer className="border-t border-slate-400 xl:py-[6px] flex w-full justify-between box-border">
          
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" 
    onClick ={()=> {
     const duplicateCheck = cart.find(item=>item.id===detail.id)
      if(!duplicateCheck){
      setCart(state=>[...state,detail])
      alert("It was added to the rental list.")
      navigate(-1)}
      else{alert("This item has already been added.")}
  }}
    strokeWidth={1.5} stroke="currentColor" className="w-8 h-8 2xl:w-14 2xl:h-14 hover:text-pink-300">
    <path strokeLinecap="round" strokeLinejoin="round" 
    d="M20.25 7.5l-.625 10.632a2.25 2.25 0 01-2.247 2.118H6.622a2.25 2.25 0 01-2.247-2.118L3.75 7.5M10 11.25h4M3.375 7.5h17.25c.621 0 1.125-.504 1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125z" />
    </svg>
    
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" onClick = {()=>navigate(-1)}
    strokeWidth={1.5} stroke="currentColor" className="w-8 h-8 2xl:w-14 2xl:h-14 hover:text-pink-300">
    <path strokeLinecap="round" strokeLinejoin="round" 
    d="M12 9.75L14.25 12m0 0l2.25 2.25M14.25 12l2.25-2.25M14.25 12L12 14.25m-2.58 4.92l-6.375-6.375a1.125 1.125 0 010-1.59L9.42 4.83c.211-.211.498-.33.796-.33H19.5a2.25 2.25 0 012.25 2.25v10.5a2.25 2.25 0 01-2.25 2.25h-9.284c-.298 0-.585-.119-.796-.33z" />
    </svg></footer>
   )
}
export default DetailSvg