import { Link } from "react-router-dom"
import { motion } from "framer-motion"
import { movieListProps } from "../type/props"

const MovieList = (item:movieListProps) =>{
    return(
    <div className="mt-2 mx-auto shadow-xl shadow-slate-400 p-2 rounded-lg hover:ring hover:ring-slate-300 hover:ring-offset-slate-200">
                <Link to ={item.link} onClick = {()=>{item.setId(item.id+"")}}>
                <motion.img src = {`https://image.tmdb.org/t/p/w500/${item.poster_path}`} 
                alt="" layoutId={item.title+""} animate={{opacity:1}} exit={{opacity:0}}
                className="2xl:w-80 xl:w-60 w-44 rounded-xl "/></Link>
                <h3 className="2xl:text-2xl text-lg font-bold w-20 truncate">{item.title}</h3>
            </div>
    )
}

export default MovieList