import {useLocation, useNavigate} from "react-router-dom" 
import { useRecoilValue } from "recoil"
import { cartLength } from "../atom"
const Header = () => {    
const navigate = useNavigate()
const {pathname} = useLocation()
const cartListLength = useRecoilValue(cartLength)
return <div 
className="fixed top-0 w-full bg-white flex justify-between px-8 pt-5 border border-b-gray-200 shadow-lg shadow-slate-200 rounded-b-xl">
<div className="mx-auto hover:text-pink-300 mb-2" onClick ={() => navigate('/')}>
<img src="\favicon copy.ico" alt="" className="w-10 ml-1"/> 
<h3 className={`text-lg font-bold hover:text-pink-300 ${pathname==="/"?"text-pink-300":"text-black"}`}>vercel</h3>
</div>
<div className="flex pt-4 relative">
<svg xmlns="http://www.w3.org/2000/svg" fill="none" onClick = {()=>navigate('/cart')}
viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={`w-8 h-8 mr-4 hover:text-pink-300 ${pathname==="/cart"?"text-pink-300":"text-black"}`}>
  <path strokeLinecap="round" strokeLinejoin="round" d="M20.25 7.5l-.625 10.632a2.25 2.25 0 01-2.247 2.118H6.622a2.25 2.25 0 01-2.247-2.118L3.75 7.5M10 11.25h4M3.375 7.5h17.25c.621 0 1.125-.504 1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125z" />
</svg>
{cartListLength?<span className="text-xs font-bold rounded-full absolute bg-red-500 text-white w-4 text-center pb-[0.1rem] top-3 left-[1.1rem]">{cartListLength}</span>:null}
<svg xmlns="http://www.w3.org/2000/svg" fill="none" onClick = {()=>navigate('/search')}
viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className = {`w-8 h-8 hover:text-pink-300 ${pathname==="/search"?"text-pink-300":"text-black"}`}>
<path strokeLinecap="round" strokeLinejoin="round" 
d="M15.75 15.75l-2.489-2.489m0 0a3.375 3.375 0 10-4.773-4.773 3.375 3.375 0 004.774 4.774zM21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
</svg>
</div>
</div>
}
export default Header