import {atom, selector, selectorFamily} from "recoil"
import { v1 } from "uuid"

export const movieData = atom<movieType[]|[]>({
    key:`movie fetching ${v1()}`,
    default:[]
})

export const cartData = atom<movieType[]|[]>({
    key:`movie cart ${v1()}`,
    default:[]
})

export const cartLength = selector({
    key:`cart length ${v1()}`,
    get:({get})=> get(cartData).length,
    // set:({set},value)=>{}
})

export const searchResult = selectorFamily({
    key:`search result ${v1()}`,
    get:(text:string)=>({get})=>get(movieData).filter(item=>{ return(
        item.title?.toLowerCase().replace(/ /g, '').includes(text?.toLowerCase().replace(/ /g, ''))
        )})
    })