import React from "react"
import { useNavigate, useOutletContext, useParams } from "react-router-dom"
import { motion } from "framer-motion"
import { useQuery } from "react-query"
import { queryOption } from "../duplicateTs/option"
import { Helmet } from "react-helmet"
import { detailQueryFn } from "../duplicateTs/qureyFunction"
import DetailErrorText from "../duplicateTsx/DetailErrorText"
import DetailBord from "../duplicateTsx/DetailBord"
const Detail = () => {
    const navigate = useNavigate()
    const params = useParams()
    const detailId = useOutletContext<string>()
    const {isLoading,data:detail,isError:detailError} = useQuery("detail_data" , 
    ()=>detailQueryFn(detailId),{...queryOption,retry:1})
    return (<><Helmet><title>
      {isLoading?"loading...":detailError?"404pages":detail?.title}
      </title></Helmet>
    <div className="flex justify-center items-center">
        <div className="bg-black w-full h-full fixed top-0 opacity-70" onClick = {()=>navigate(-1)}/>
        <motion.div className="fixed xl:flex block 2xl:top-14 top-10 2xl:w-[45rem] xl:w-[32rem] w-[18rem] aspect-[9/16] xl:overflow-hidden overflow-auto xl:aspect-square bg-white rounded-3xl"
        layoutId = {params.movie_title||params.search_title}>
          {detailError?<DetailErrorText/>:<DetailBord {...detail}/>}
        </motion.div>
    </div></>
    )
}
export default Detail