import { useForm } from "react-hook-form"
import { useRecoilValue } from "recoil"
import { movieData, searchResult } from "../atom"
import { useState } from "react"
import { Outlet, useMatch } from "react-router-dom"
import { AnimatePresence } from "framer-motion"
import { Helmet } from "react-helmet"
import { searchEngine } from "../duplicateTsx/search_engine"
import FormState from "../duplicateTsx/FormState"
    
    const Search = () => {
    const [id,setId] = useState("")
    const [watch,setWatch] = useState({movie_title:""})
    const {formState:{errors},setError,clearErrors} = useForm<search>()
    const movieSearchResult = useRecoilValue(searchResult(watch.movie_title))
    const defaultMovieResult = useRecoilValue(movieData)
    const match = useMatch("/search/:search_title")
    return(<>
    <Helmet><title>search</title></Helmet>
    <main className="mt-28 w-full mb-7">
    <FormState setError={setError} clearErrors={clearErrors} setWatch={setWatch}/>
    {errors.movie_title?
    (<div className="text-center pt-2 pr-12 text-red-500 font-bold">{errors.movie_title?.message}</div>):
    (<div className="text-center pt-2 pr-12 text-red-500 font-bold">{errors.movie_title_result?.message}</div>)}
    <article className={`${movieSearchResult.length?"grid xl:grid-cols-4":watch.movie_title?"flex item-center justify-center":"grid xl:grid-cols-4"} grid-cols-2 mt-2`}>
    {movieSearchResult.length?searchEngine({result:movieSearchResult,setId})
    : watch.movie_title? <div className="text-center text-3xl font-bold pt-32 pr-12 text-blue-500 ">Not a result</div>:
    searchEngine({result:defaultMovieResult,setId})}
    </article>
    <AnimatePresence>{match?<Outlet context={id}/>:null}</AnimatePresence>
    </main>
    </>)
}
export default Search