const DetailSection = (detail:movieType) => {
    return(<>
    <h4 className="text-center font-bold border-b border-slate-400 py-3 2xl:text-3xl">{detail?.title}</h4>
          <div className="flex justify-end mt-2 font-bold 2xl:text-2xl">Date : {detail?.release_date}</div>
          <div className="flex justify-end font-bold 2xl:text-2xl">Runtime : {detail?.runtime}</div>
          <div className="flex justify-end font-bold mb-2 2xl:text-2xl">Grade : {Math.ceil(detail?.vote_average)}</div>
          <h6 className="border-t border-slate-400 p-2 text-justify 2xl:text-2xl font-bold overflow-auto xl:min-h-[51vh] xl:max-h-[51vh] scrollbar-none">{detail?.overview}</h6>
    </>)
}
export default DetailSection