import DetailSection from "./detail_bord_section/DetailSection"
import DetailSvg from "./detail_bord_section/DetailSvg"

const DetailBord = (detail:movieType) => {
    return(<>
    <img src = {`https://image.tmdb.org/t/p/w500/${detail?.poster_path}`} 
          className="xl:h-full xl:aspect-[1/2] h-64 aspect-[4/3]" alt=""/>
          <main className="mx-4">
          <DetailSection {...detail}/>
          <DetailSvg {...detail}/>
          </main>
    </>)
}
export default DetailBord