import { useNavigate } from "react-router-dom"

const DetailErrorText = () => {
    const navigate = useNavigate()
    return(<div className="w-full aspect-square flex items-center justify-center flex-col">
    <h1 className="font-bold text-xl">404 pages... <br/> nothing pages </h1>
    <input type="button" value="home" className="mt-5 bg-blue-500 font-bold text-lg p-2 text-white rounded-xl hover:opacity-80"
    onClick={()=>navigate('/')}/></div>)
}
export default DetailErrorText