import { useEffect,useState} from "react" 
import { movieData } from "./atom"
import { AnimatePresence} from "framer-motion"
import { useQuery } from "react-query"
import { useRecoilState } from "recoil"
import { Outlet, useMatch } from "react-router-dom"
import { queryOption } from "./duplicateTs/option"
import { mainQueryFunction } from "./duplicateTs/qureyFunction"
import { Helmet } from "react-helmet"
import MovieList from "./components/MovieList"
const Main = () => {
    
    const [id,setId] = useState("")
    const [state,setState] = useRecoilState(movieData)
    const {isLoading,data:movies,isError} = useQuery<page_result>("movie_data",mainQueryFunction,queryOption)
    useEffect(()=>setState(movies?.results as movieType[]),[movies,setState])
    const match = useMatch("/:movie_title")
    return(<>
    <Helmet><title>{isLoading?"loading...":isError?"error":"vercel"}</title></Helmet>
      {isLoading?<h3 className="mt-28 h-72 font-bold text-3xl flex w-full items-center justify-center text-orange-500">loading...</h3>:
       <main className="grid xl:grid-cols-4 grid-cols-2 mt-28 mb-7">
        {state?.map(item=>{
            return(<MovieList {...item} key={item.id} setId={setId} link={`/${item.title}`} />)})}
        </main>} 
     <AnimatePresence>{match?<Outlet context={id}/>:null}</AnimatePresence>
    </>)} 
export default Main