import axios from "axios"

export const mainQueryFunction = async() => {
  return await(await axios(`https://api.themoviedb.org/3/movie/popular?api_key=${process.env.REACT_APP_KEY}`)).data
}

export const detailQueryFn = async(detailId:string) => {
    return await(await axios(`https://api.themoviedb.org/3/movie/${detailId}?api_key=${process.env.REACT_APP_KEY}`)).data
}